@import url(https://fonts.googleapis.com/css?family=Open+Sans|Roboto);
@media only screen
and (min-width: 900px) {
  body {
    display: none;
  }
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* p {
  font-family: 'Open Sans', sans-serif;
} */

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.back-button__icon {
  fill: #FFF;
}

.page__background {
  background-color: #F9F9F9;
}

.toolbar--material__left:empty,
.toolbar--material__right:empty {
  min-width: 15%;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.LocationsPage-thumbnail {
  border-radius: 20%;
}

.LocationsPage-checkmark {
  margin-bottom: -50%;
  margin-left: -25%;
  border-radius: 45%;
  background-color: #FFF;
  color: #008000;
}
.NavBar {
  background-color: #42BDFF;
}

.NavBar-left span {
  color: #FFF;
}

.NavBar-center {
  width: 70%;
  font-family: 'Roboto', sans-serif;
  font-size: 20px;
  color: #FFFFFF;
  text-align: center;
}

.ActivityListPage-thumbnail {
  height: auto;
}

.ActivityListPage-task_title {
  font-size: 18px;
}

.ActivityListPage-checkmark {
  margin-bottom: -35%;
  margin-left: 25%;
  border-radius: 45%;
  background-color: #FFF;
  color: #008000;
}
.LocationPage-QR_description {
  text-align: center;
  padding: 0 1rem;
  font-size: 28px;
  font-weight: 300;
  color: #1f1f21;
}

.LocationPage-QR_cancel_btn {
  margin-top: 1rem;
  background-color: #ED1D24;
}
.LoginPage {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  height: 100%;
  background-color: #42BDFF;
}

.LoginPage-title {
  font-size: 2rem;
  text-align: center;
  color: #FFF;
}

.LoginPage-form {
  width: 80%;
  -webkit-flex-basis: 50%;
          flex-basis: 50%;
}

.LoginPage-input {
  width: 95%;
  margin: 0.3rem 0;
  padding: 0.2rem 0.5rem;
  border-radius: 5px;
  background-color: #FFF;
}

.LoginPage-input input {
  font-size: 18px;
}

.LoginPage-login_btn {
  margin: 0.3rem 0;
  font-size: 18px;
  background-color: #E91E63;
}

.LoginPage-signup_btn {
  margin-top: 0.5rem;
  font-size: 18px;
  text-transform: none;
  color: #FFF;
}
.SignUpPage-register_btn {
  margin: 0.3rem 0;
  font-size: 18px;
  background-color: #0CB34C;
}
